<template>
  <div class="all-container" ref="trys" style="height:100%">
      <div class="tabs-show tabs-shows" style="margin:0.8em 0 0 0">
        <datePicker :dateOpt="searchOption" @getDatePicker="getFrom" ref="search.searchDate"></datePicker>
      </div>
      <div class="" style="height:18em;background: transparent;">
        <div>
          <div class="lineCharts1" id="lineCharts" v-if="showLine">
            <div  class="emptyShowPic">
              <div class="emptyContent">
                <img src="dist/img/empty-data.png">
                <p>查不到相关数据</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="" style="font-size: 0.94em;height: calc(100% - 20em);background: transparent;margin-top:0.5em">
        <div ref="myOwnTable" class="table-container" style="height:calc(100% )">
          <tables ref="myTable" class="dialogTable" v-if="tableShow" :tableOpt="tableOption" @rowDbClick="getDetails"
                  style="margin:0em;width: 100%;"></tables>
        </div>
      </div>
    <dialogs type="alert" ref="alerts"></dialogs>
    <!--采集结果-->
    <dialogs type="customer" ref="resultData" class="dialogTable " width="54em" maxHeight="600px" >
      <div slot="header">
        采集结果
      </div>
      <div slot="content" class="tableContents">
        <div class="tabs-show tab-div " style="margin:0 0 0.6em 0!important;float:left;">
          <ul>
            <li :class="{'selectedLi': tabIndex === $index}"
                v-for="(myBtn, $index) in btnGroup" @click="getTab($index)">
              {{myBtn.name}} ( {{myBtn.num}} )
            </li>
          </ul>
        </div>

        <div class="tabContent" style="height:340px;float: left;padding-top:0;font-size:1.1em">
          <tables ref="resoluteTable" :tableOpt="resultOpt" @getTotals="getTotals" v-if="resultShow"></tables>
        </div>
      </div>
    </dialogs>
  </div>
</template>
<script>
//  import echarts from 'echarts'
  export default {
    data() {
      return {
        resultShow:false, //采集详情中的表格
        showLine: true,
        search: {searchDate: ""},
        searchOption: {
          key: 'search.searchDate',
          width: '10em',
          align: 'right'
        },
        showLine: true, //柱状图无数据显示
        line: '', //折线图dom
        tableShow: false, //table延迟加载
        show: true,
        tableOption: {
          rowName: 'data',
          column: [],
          queryType: 'form',
          pageSize: 9999,
          pageNumber: 1,
          url: 'keyCollection/analysis',
          method: 'post',
          queryParam: {},
          isExport: true,
          exportURL: 'keyCollection/downKeyCollectionAnalysis'
        },
        btnGroup: [
          {name: '采集成功', num: 0,  url: 'keyCollection/collectSucc'},
          {name: '采集失败', num: 0,  url: 'keyCollection/collectFail'}],
        resultOpt: {
          rowName: 'data',
          column:null ,
          pageSize: 99999,
          pageNumber: 1,
          url: 'realTimeData/querySuccessList',
          method: 'post',
          isExport: true,
          height: 260,
          exportURL: 'realTimeData/generateStatusExcel',
          noFooter:true,
          queryParam: ''
        },
        resultRow:null,
        tabIndex: 0,
        controls: []
      }
    },
    computed: {
      columns: function () {
        return [
          {
            title: '抄表时间',
            field: 'collectTime',
            textAlign: 'center',
            width: '200'
          },
          {
            title: '抄表总数',
            field: 'sumCount',
            textAlign: 'center',
            width: '150'
          },
          {
            title: '失败数',
            field: 'failCount',
            textAlign: 'center',
            width: '150'
          },
          {
            title: '成功率 (%)',
            field: 'successRate',
            textAlign: 'center',
            type: "string",
            width: '150',
            formatter: function (val, index, row, obj) {
              //return (val * 100).toFixed(2);
              // if(val !== '' && val !== null && typeof val !== undefined) {
              //   return val + '%'
              // }
			        return val
            }
          },
          {
            title: '上报成功率 (%)',
            field: 'radioRate',
            textAlign: 'center',
            type: "string",
            width: '150',
            formatter: function (val, index, row, obj) {
              //return (val * 100).toFixed(2);
              // if(val !== '' && val !== null && typeof val !== undefined) {
              //   return val + '%'
              // }
			        return val
            }
          },
          {
            title: '召测成功率 (%)',
            field: 'callRate',
            textAlign: 'center',
            type: "string",
            width: '150',
            formatter: function (val, index, row, obj) {
              //return (val * 100).toFixed(2);
              // if(val !== '' && val !== null && typeof val !== undefined) {
              //   return val + '%'
              // }
			        return val
            }
          }
        ]
      },
      restCols: function () {
       let successCol=[
          {title: '采集时间', textAlign: 'center', width: '200', field: 'collectTime'},
          {title: '客户名称', textAlign: 'center', width: '200', field: 'customerName'},
          {title: '表计编号', textAlign: 'center', width: '200', field: 'deviceNo'},
          {title: '表计户号', textAlign: 'center', width: '200', field: 'meterUserNo'},
          {title: '节点编号', textAlign: 'center', width: '200', field: 'collectNo'},
          {title: '客户地址', textAlign: 'center', width: '200', field: 'customerAddr'},
          {title: '安装位置', textAlign: 'center', width: '200', field: 'installAddr'}];
        let failCol=[{title: '客户名称', textAlign: 'center', width: '200', field: 'customerName'},
          {title: '表计编号', textAlign: 'center', width: '200', field: 'deviceNo'},
          {title: '表计户号', textAlign: 'center', width: '200', field: 'meterUserNo'},
          {title: '节点编号', textAlign: 'center', width: '200', field: 'collectNo'},
          {title: '客户地址', textAlign: 'center', width: '200', field: 'customerAddr'},
          {title: '安装位置', textAlign: 'center', width: '200', field: 'installAddr'}];

        return [successCol,failCol];
      },
    },
    methods: {
      getFrom: function (reval) {
        this.search.searchDate = reval.val;
        if (this.$refs.myTable) {
          this.makeLine();//请求曲线数据
          this.$refs.myTable.resetTableOpt({queryParam: {searchDate: this.search.searchDate}});
        }
      },
      //左侧柱状图，各时间段的召测情况
      makeLine: function () {
        let that = this;
        let option = {
          color: [
            '#5EAEE3',
            '#c23531',
            '#7d3b9e',
            '#2f4554',
            '#61a0a8',
            '#d48265',
            '#91c7ae',
            '#749f83',
            '#ca8622',
            '#bda29a',
            '#6e7074',
            '#546570',
            '#c4ccd3'
          ],
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              crossStyle: {
                color: '#999'
              }
            }
          },
          toolbox: {
            show: false,
            x: 'center',
            feature: {
              dataView: {show: true, readOnly: false},
              magicType: {show: true, type: ['line', 'bar']},
              restore: {show: true},
              saveAsImage: {show: true}
            }
          },
          grid: {
            top: 40,
            bottom: 60,
            left: 70,
            right: 50
          },
          legend: {
            data: ['失败数', '成功率'],
            top: 'bottom',
            show: false
          },
          dataZoom: [
            {
              startValue: '00:00',
              start: 0,
              end: 20
            },
            {
              type: 'slider',
              handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
              handleSize: '80%',
              handleStyle: {
                color: '#fff',
                shadowBlur: 3,
                shadowColor: 'rgba(0, 0, 0, 0.6)',
                shadowOffsetX: 2,
                shadowOffsetY: 2
              },
              backgroundColor: '#4E5A6A',
              fillerColor: '#777E88',
              borderColor: '#4E5A6A',
              textStyle: {
                color: 'white'
              }
            }],
          xAxis: {
            type: 'category',
            data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
            axisPointer: {
              type: 'shadow'
            },
            axisLabel: {
              textStyle: {
                color: '#fff'
              }
            },
            axisLine: {
              lineStyle: {
                color: '#576373'
              }
            }
          },
          yAxis: [
            {
              type: 'value',
              name: '成功率（%）',
              nameTextStyle: {
                color: '#fff',
              },
              min: 0,
              max: 100,
              axisLabel: {
                color: '#fff',
                formatter: '{value} %',
                textStyle: {
                  color: '#fff'
                }
              },
              axisLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              splitLine: {
                lineStyle: {
                  color: '#576373'
                }
              }
            },
            {
              type: 'value',
              name: '失败数',
              nameTextStyle: {
                color: '#fff'
              },
              min: 0,
              axisLabel: {
                textStyle: {
                  color: '#fff'
                }
              },
              splitLine: {
                lineStyle: {
                  color: '#576373'
                }
              },
              axisLine: {
                show: false
              },
              axisTick: {
                show: false
              }
            },
          ],
          series: [
            {
              name: '失败数',
              type: 'bar',
              yAxisIndex: 1,
              label: {
                normal: {
                  position: 'top',
                  show: true,
                  color: '#fff',
                  // backgroundColor: '#6E7786',
                  padding: [7, 15, 14, 15],
                  borderRadius: 5,
                  backgroundColor: {
                    image: 'dist/img/realTime/pop-up.png'
                  }
                }
              },
              itemStyle: {
                normal: {
                  color: new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#F96362'},
                      {offset: 0.5, color: '#CC5A5E'},
                      {offset: 1, color: '#974F5A'}
                    ]
                  )
                },
                emphasis: {
                  color: new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#2378f7'},
                      {offset: 0.7, color: '#2378f7'},
                      {offset: 1, color: '#83bff6'}
                    ]
                  )
                }
              },
              data: [2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4, 3.3]
            },
            {
              name: '成功率',
              type: 'line',
              data: [2.0, 2.2, 3.3, 4.5, 6.3, 10.2, 20.3, 23.4, 23.0, 16.5, 12.0, 6.2]
            }
          ]
        };
        that.getRequest({
          url: 'keyCollection/analysis',
          method: 'post',
          param: {searchDate: that.search.searchDate},
          success: function (data) {
            let xValue = [];
            let yValue = {
              first: '',
              other: '',
              succRate: ''
            };
            let first = [], other = [], succRate = [];
            if (data.data && data.data.length > 0) {
              if (that.line !== null && that.line !== "" && that.line !== undefined) {
                that.line.dispose();
              }
              that.line = echarts.init(document.getElementById('lineCharts'));
              for (let i = 0; i < data.data.length; i++) {
                let item = data.data[i];
                //let rate =(item.successRate * 100).toFixed(2);
                let rate = item.successRate;
                succRate.push(rate);
                xValue.push(item.collectTime.split(' ')[1]);
                first.push(item.failCount);
                other.push(item);
              }
              yValue.first = first;
              yValue.other = other;
              yValue.succRate = succRate;
              option.xAxis.data = xValue;
              option.series[0].data = yValue.first;
              option.yAxis[0].min = Math.min.apply(null, yValue.succRate);
              option.series[1].data = yValue.succRate;
              option.tooltip.formatter = function (params) {
                let tar = params[0];
                let other = yValue.other[tar.dataIndex];
                return ' 时间：' + other.collectTime + ' <br/> 失败数：' + tar.value +
                  '<br/> 成功率：' + other.successRate +'% <br/>';
              };
              that.$nextTick(function () {
                 that.line.setOption(option, true);
              })

            } else {
              that.line.dispose();
              that.showLine = false;
              setTimeout(function () {
                   that.showLine = true;
              }, 0)
            }
          },
          error: function () {
            that.showLine = false;
	    setTimeout(function () {
                   that.showLine = true;
              }, 0)
          }
        }, true)
      },
      //打开详细
      getDetails:function(items){
        this.resultRow=items.val;
        this.resoluteTableShow();
        this.btnGroup[1].num =  this.resultRow.failCount;
      },
      //#region 采集详情界面
      //关闭召测结果窗口
      closeResult: function () {
        this.$refs.resultData.close();
        this.resultShow = false;
        this.tabIndex = 0;
        this.btnGroup[0].num =  this.btnGroup[1].num = 0;
      },
      //打开召测结果窗口
      resoluteTableShow: function() {
        let that=this;
        this.resultOpt.queryParam = {queueGuid: this.resultRow.queueGuid,collectDate:this.resultRow.collectTime.split(' ')[0]};
        this.resultOpt.url =  this.btnGroup[0].url;
        this.resultOpt.column =  this.restCols[0];
        this.resultShow = true;
        this.$refs.resultData.show({
          hasClose: true,
          action: {
            cancel: function () {
              that.closeResult()
            }
          }
        });
      },
    
      //召测结果tab切换页
      getTab: function (index) {
        this.tabIndex = index;
        let columns = this.restCols[index];
        this.$refs.resoluteTable.resetTableOpt({
          queryParam: {queueGuid:this.resultRow.queueGuid, collectDate:this.resultRow.collectTime.split(' ')[0]},
          column:  columns,
          url: this.btnGroup[index].url
        });
      },
      //召测结果数量显示
      getTotals: function (val) {
        this.btnGroup[this.tabIndex].num = val;
      },
      resizeCon: function () {
        let that = this;
        that.line !== '' ? that.line.resize() : '';
        if ( that.$refs.myTable) {
          that.$refs.myTable.onResize();
        }
      },
      windowChange: function () {
        let that = this;
        that.resizeCon();
        // window.onresize = function () {
        //   that.resizeCon();
        // }
      },
      init: function () {
         let that=this;
        this.tableOption.isExport = this.controls[6];
        this.tableOption.column = this.columns;
        this.tableOption.queryParam = {searchDate:this.search.searchDate};
        this.tableOption.height = this.$refs.myOwnTable.clientHeight - 60;
        that.tableShow=true;
        this.makeLine();//请求曲线数据
        // window.onresize = function () {
        //     that.windowChange();
        // }
      }
      //#endregion
    },
    mounted:function(){
      this.isIn();
      this.getAction();
    },
    created: function () {
      
    }
  }
</script>
<style lang="scss">
  $headerColor: #44BBDD;
  .info-container1 {
    margin-top: 5px
  }

  .lineCharts1 {
    width: calc(100% - 2px);
    height: 14.5em;
    float: left;
  }
   .tabs-shows {
    margin: 0.8em 0em 0 0;
    float: right;
    .date-picker{
      .date-button{
        line-height:1.9;
        .icon-date{
          margin-top:-0.1em!important
        }
      }
    }
  }
</style>



